import React from "react";
import Spinner from "react-bootstrap/Spinner";
import { toast } from 'react-toastify';
import FloatingTextInput from "views/mobileView/components/atoms/FloatingTextInput";
import { emailValidator } from "libs/validators";
import AdminServices from "services/api/AdminServices";
import AuthHeader from "views/mobileView/components/molecules/Headers/AuthHeader";
import { AiOutlineRight } from 'react-icons/ai';
export default function SignupScreen(props) {
	const [email, setEmail] = React.useState('');
	const [mobile, setMobile] = React.useState('');
	const [loginUsingEmail, setLoginUsingEmail] = React.useState(false)
	const [nextBtnPressed, setNextBtnPressed] = React.useState(false)

	React.useEffect(() => {
		var aa = localStorage.getItem("lastEmail")
		setEmail(aa !== null ? aa : '');
		var x = localStorage.getItem("lastMobile");
		setMobile(x !== null ? x : '')
	}, []);

	const validateInput = () => {
		if (loginUsingEmail) {
			return emailValidator(email).isValid
		}
		else {
			return mobile.length === 10
		}
	}

	const onNextBtnPressed = async () => {
		setNextBtnPressed(true);
		if (loginUsingEmail) {
			try {
				const res = await AdminServices.clientExists(email, "EMAIL").then((res) => res);
				if (res.type) {
					console.log("client exists API ", res.data)
					if (res.data.ClientId !== '') {
						localStorage.setItem("lastEmail", res.data.Email)
						localStorage.setItem("lastMobile", res.data.MobileNumber)
						props.history.push({
							pathname: '/login-otp',
							state: { ...res.data }
						})
						setNextBtnPressed(false);
					} else {
						localStorage.setItem("lastEmail", email)
						props.history.push({
							pathname: '/first-name',
							state: { email, verify: "EMAIL" }
						})
						setNextBtnPressed(false);
					}
				} else {
					toast.error("Try again ...");
					setNextBtnPressed(false);
				}
			} catch (error) {
				toast.error("Try again ...");
				setNextBtnPressed(false);
			}
		}
		else {
			try {
				const res = await AdminServices.clientExists(mobile, "MOBILE").then((res) => res);
				if (res.type) {
					console.log("client exists API ", res.data)
					if (res.data.ClientId !== '') {
						localStorage.setItem("lastEmail", res.data?.Email)
						localStorage.setItem("lastMobile", res.data?.MobileNumber)
						props.history.push({
							pathname: '/login-otp',
							state: { ...res.data }
						})
						setNextBtnPressed(false);
					} else {
						localStorage.setItem("lastMobile", mobile)
						props.history.push({
							pathname: '/first-name',
							state: { mobile, verify: "MOBILE" }
						})
						setNextBtnPressed(false);
					}
				} else {
					toast.error("Try again ...");
					setNextBtnPressed(false);
				}
			} catch (error) {
				toast.error("Try again ...");
				setNextBtnPressed(false);
			}
		}
	};
	return (
		<div className="mainContainer">
			<AuthHeader />
			<div className="dataContainer">
				<div className="login-header">
					<div className="bigHeader">{loginUsingEmail ? "Login/Sign up using email" : "Login/Sign up using mobile number"}</div>
					<div className="bodyTextDark" style={{ marginTop: "6px" }}>{loginUsingEmail ? "Please enter your email address" : "Please enter your 10 digit Indian mobile number"}</div>
				</div>
				{loginUsingEmail ?
					<FloatingTextInput
						type="email"
						label="Email address"
						autoFocus
						onChange={(p) => setEmail(p.target.value)}
						value={email}
					/> :
					<FloatingTextInput
						type="tel"
						label="Mobile number"
						autoFocus
						id="Mobile-number"
						placeholder=" "
						maxLength={10}
						value={mobile}
						onChange={(num) => {
							const newMobile = num.target.value.replace(/\D/g, '');
							setMobile(newMobile);
						}}
					/>
				}
				<div className="header2dark secondaryGreen" style={{ marginTop: "30px" }} onClick={() => { setLoginUsingEmail(!loginUsingEmail) }}>
					{loginUsingEmail ? "Use mobile number instead" : "Use email instead"} <AiOutlineRight style={{ verticalAlign: "unset" }} size={10} />
				</div>

			</div>
			<div className="login-footer">
				{
					nextBtnPressed ?
						<button className="solidButton" style={{ opacity: 0.4 }}>
							<Spinner
								style={{ color: "#fff" }}
								as="span"
								animation="border"
								size="sm"
								role="status"
								aria-hidden="true"
							/>
						</button>
						:
						<button className="solidButton" style={{ opacity: validateInput() ? 1 : 0.4 }}
							onClick={() => { validateInput() && onNextBtnPressed() }}>
							Next <AiOutlineRight size={12} />
						</button>
				}
			</div>
		</div>
	);
}
